"use client";
import { useState } from "react";
import axios from "axios";

export default function AddProduct() {
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [category, setCategory] = useState("");
  const [variants, setVariants] = useState([]);

  const handleAddVariant = () => {
    setVariants([...variants, { name: "", colors: [] }]);
  };

  const handleAddColor = (variantIndex) => {
    const updated = [...variants];
    updated[variantIndex].colors.push({ name: "", price: "", image: null });
    setVariants(updated);
  };

  const handleVariantChange = (index, key, value) => {
    const updated = [...variants];
    updated[index][key] = value;
    setVariants(updated);
  };

  const handleColorChange = (vIndex, cIndex, key, value) => {
    const updated = [...variants];
    updated[vIndex].colors[cIndex][key] = value;
    setVariants(updated);
  };

  const handleFileChange = (vIndex, cIndex, file) => {
    const updated = [...variants];
    updated[vIndex].colors[cIndex].image = file;
    setVariants(updated);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const formData = new FormData();
    formData.append("name", name);
    formData.append("description", description);
    formData.append("category", category);

    const structuredVariants = variants.map((v, vIndex) => ({
      name: v.name,
      colors: v.colors.map((c, cIndex) => ({
        name: c.name,
        price: c.price,
        imageFieldName: `variant${vIndex}-color${cIndex}`,
      })),
    }));

    formData.append("variants", JSON.stringify(structuredVariants));

    variants.forEach((v, vIndex) => {
      v.colors.forEach((c, cIndex) => {
        if (c.image) {
          formData.append(`variant${vIndex}-color${cIndex}`, c.image);
        }
      });
    });

    try {
      const res = await axios.post("/api/product/add", formData);
      alert(res.data.message);
      window.location.href = "/seller/products";
    } catch (err) {
      alert("Upload failed: " + (err.response?.data?.message || err.message));
    }
  };

  return (
    <div className="max-w-4xl mx-auto p-6">
      <h2 className="text-2xl font-bold mb-4">Add New Product</h2>

      <form onSubmit={handleSubmit} className="space-y-4">
        <input
          type="text"
          placeholder="Product Name"
          className="w-full p-3 border rounded"
          value={name}
          onChange={(e) => setName(e.target.value)}
          required
        />
        <textarea
          placeholder="Description"
          className="w-full p-3 border rounded"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          required
        />
        <input
          type="text"
          placeholder="Category"
          className="w-full p-3 border rounded"
          value={category}
          onChange={(e) => setCategory(e.target.value)}
          required
        />

        <div>
          <h3 className="text-xl font-semibold mb-2">Variants</h3>
          {variants.map((variant, vIndex) => (
            <div key={vIndex} className="border p-4 rounded mb-4 bg-gray-50">
              <input
                type="text"
                placeholder="Variant Name (e.g., S-Line)"
                className="w-full mb-3 p-2 border rounded"
                value={variant.name}
                onChange={(e) =>
                  handleVariantChange(vIndex, "name", e.target.value)
                }
                required
              />
              {variant.colors.map((color, cIndex) => (
                <div
                  key={cIndex}
                  className="grid grid-cols-1 md:grid-cols-3 gap-3 mb-3"
                >
                  <input
                    type="text"
                    placeholder="Color Name"
                    className="p-2 border rounded"
                    value={color.name}
                    onChange={(e) =>
                      handleColorChange(vIndex, cIndex, "name", e.target.value)
                    }
                    required
                  />
                  <input
                    type="number"
                    placeholder="Price"
                    className="p-2 border rounded"
                    value={color.price}
                    onChange={(e) =>
                      handleColorChange(
                        vIndex,
                        cIndex,
                        "price",
                        e.target.value
                      )
                    }
                    required
                  />
                  <input
                    type="file"
                    accept="image/*"
                    className="p-2"
                    onChange={(e) =>
                      handleFileChange(vIndex, cIndex, e.target.files[0])
                    }
                    required
                  />
                </div>
              ))}
              <button
                type="button"
                className="text-blue-600 mt-2 text-sm"
                onClick={() => handleAddColor(vIndex)}
              >
                + Add Color
              </button>
            </div>
          ))}
          <button
            type="button"
            className="text-green-600 text-sm"
            onClick={handleAddVariant}
          >
            + Add Variant
          </button>
        </div>

        <button
          type="submit"
          className="bg-blue-600 hover:bg-blue-700 text-white px-6 py-2 rounded"
        >
          Submit Product
        </button>
      </form>
    </div>
  );
}
